// First step : Import Libraries -> React
import * as React from 'react'

// study syntax and structure of react
const hello = <h1>Hello World!</h1>

// object oriented component? define
const Greeting = () => {
    return <h1>Hello World!</h1>
}


// Head를 지정하지 않았는데 어떻게 표시될까?
// component exportation to use in my app
export default Greeting